<template>
  <div id="app">
    <h2>Welcome in Vue Highcharts Wrapper Demo</h2>
    <hr style="width: 200px; margin: 100px auto;">
    <h3>Select chart constructor:</h3>
    <div class="button-grp">
      <p>Random number from backend: {{ randomNumber }}</p>
      <button @click="getRandom">New random number</button>
      <button @click="select('stockChart')" :class="{btnActive: selected === 'stockChart'}">Stock Chart</button>
    </div>
    <keep-alive>
      <component :is="currentView"/>
    </keep-alive>
  </div>
</template>

<script>
  import StockChart from './../components/StockChart'
  import axios from 'axios'
  export default {
    name: 'app',
    data () {
      return {
        selected: 'chart',
        currentView: 'chart'
      }
    },
    components: {
      stockChart: StockChart,
    },
    methods: {
      activate (elem) {
        this.selected = elem
      },
      handler () {
        for (let arg of args) {
          if (arg instanceof Function) {
            arg()
          }
        }
      },
      select (elem) {
        this.currentView = elem;
        this.activate(elem)
      },
      getRandom () {
        // this.randomNumber = this.getRandomInt(1, 100)
        this.randomNumber = this.getRandomFromBackend()
      },
      getRandomFromBackend () {
        const path = `http://localhost:5000/tf_api/test/random`;
        axios.get(path)
          .then(response => {
            this.randomNumber = response.data.randomNumber
          })
          .catch(error => {
            console.log(error)
          })
      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin: 0 auto;
    margin-top: 60px;
    width: 70%;
  }
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  .row > * {
    padding: 0 20px;
  }
  .button-grp {
    margin: 20px 0;
  }
  button {
    padding: 10px 20px;
    outline: none;
    background: #828ea0;
    color: #fff;
    font-size: 16px;
    margin: 5px;
    border: 0px;
  }
  button:hover {
    background: #a7aeb8;
    transition: background 0.2s;
  }
  button:active {
    color: rgb(117, 117, 117);
  }
  .btnActive {
    color: #6fcd98
  }
</style>



